// top module
import React, { useState, useMemo, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useTranslation } from 'react-i18next';

// UI components
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import { Fab, Grid } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import DirectionsRunIcon from '@material-ui/icons/DirectionsRun';

// child components
import UmaForm from './umaForm/index';
import ChampMeet from './champMeet/index';
import AddUmaDialog from './AddUmaDialog';
// others
import { getStorage } from '../../../functions/LocalStorage';
import { UmaOption } from '../types';

const useStyles = makeStyles((theme: Theme) => ({
  root: {},
  gridItem: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    justifyContent: 'center',
    textAlign: 'center',
  },
  fab: {
    fontSize: 20,
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

const RaceSimulator = (): JSX.Element => {
  // common hooks
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  const classes = useStyles();

  // state & selector
  const [selectedForm, setSelectedForm] = useState('');
  const [umaDataList, setUmaDataList] = useState<UmaOption[] | null>(null);

  useEffect(() => {
    const storageData = getStorage('umaDataList') as UmaOption[];
    if (storageData !== null && storageData.length !== 0) {
      setUmaDataList(storageData);
    }
  }, []);

  const formObj = useMemo(() => {
    switch (selectedForm) {
      case 'AddUma':
        return (
          <AddUmaDialog
            selectedForm={selectedForm}
            setSelectedForm={setSelectedForm}
          />
        );
      case 'UmaForm':
        return <UmaForm />;
      case 'ChampMeet':
        return <ChampMeet />;
      default:
        return <div />;
    }
  }, [selectedForm]);

  return (
    <>
      <Grid container direction="row" className={classes.root}>
        <Grid item xs={4} className={classes.gridItem}>
          <Fab
            className={classes.fab}
            color="primary"
            aria-label="AddUma"
            onClick={() => setSelectedForm('AddUma')}
            variant="extended"
          >
            <AddIcon className={classes.extendedIcon} />
            
          </Fab>
        </Grid>
        <Grid item xs={4} className={classes.gridItem}>
          <Fab
            className={classes.fab}
            color="primary"
            aria-label="UmaForm"
            onClick={() => setSelectedForm('UmaForm')}
            disabled={umaDataList === null || umaDataList.length === 0}
            variant="extended"
          >
            <EditIcon className={classes.extendedIcon} />
            
          </Fab>
        </Grid>
        <Grid item xs={4} className={classes.gridItem}>
          <Fab
            className={classes.fab}
            color="primary"
            aria-label="ChampMeet"
            disabled={umaDataList === null || umaDataList.length === 0}
            onClick={() => setSelectedForm('ChampMeet')}
            variant="extended"
          >
            <DirectionsRunIcon className={classes.extendedIcon} />
            
          </Fab>
        </Grid>
      </Grid>
      {formObj}

      <br />
      <br />
      <br />
      <br />
      <br />
      <h1>:</h1>
      <h2></h2>
      <h2> </h2>
      <h2></h2>
      <h2></h2>
      <h2></h2>
      <h2>()</h2>
      <h2></h2>
      <h2></h2>
    </>
  );
};

export default RaceSimulator;
